import React from 'react'
import './homepagecard.css'
//https://thumbs.dreamstime.com/z/programming-concept-man-hand-pen-pushing-digital-screen-node-tree-signs-155772197.jpg


function homepagecard({image, AppName, description, color, backColor, link, teqOne, teqTwo, teqThree, teqFour, teqFive, teqSix, teqSeven, teqEight}) {
    return (
 <div class="projects-flip-card">
   <a href={link}>
       <div class="projects-flip-card-inner">
             <div style={{backgroundColor: `${color}`}} class="projects-flip-card-front">
                   <div className='projects-flip-card-content-wrapper'>
                     <div className='projects-flip-card-text-content-wrapper'>
                          <div className='projects-flip-card-text-content'>
                              <div className='projects-flip-card-text-heading'>
                                 {AppName}
                              </div>
                              <div className='projects-flip-card-project-description'>
                                {description}
                              </div>
                          </div>
                     </div>
                     <div className='projects-flip-card-image-wrapper'>
                       <img src={image} className='projects-flip-card-image' alt="image"/>
                     </div>
                   </div>
           </div>

                  <div style={{backgroundColor: `${backColor}`}} class="projects-flip-card-back">
                    <div className='click-card-to-see-Project-ctn'>
                      <p className='click-card'>Click card to see live project</p>
                    </div>
                    <div className='tecnologies-heading-ctn'> 
                  <h2>Tecnologies Used</h2>
                  </div>
                  <div className='list-of-tecnologies-outter-ctn'>
                  <ul className='list-of-tecnologies-ctn'>
                    <p className='tec-name'>{teqOne}</p>
                    <p className='tec-name'>{teqTwo}</p>
                    <p className='tec-name'>{teqThree}</p>
                    <p className='tec-name'>{teqFour}</p>
                    <p className='tec-name'>{teqFive}</p>
                    <p className='tec-name'>{teqSix}</p>
                    <p className='tec-name'>{teqSeven}</p>
                    <p className='tec-name'>{teqEight}</p>
                  </ul>
                  </div>
 
    </div>
  </div>
  </a>
</div>
    )
}

export default homepagecard
